<template>
  <div>
    <mt-header title="收藏夹" fixed>
        <router-link to="/user" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <div class="container fav-main">
        <div class="fav-item" v-for="item in favList">
        <a href="#" @click="jumpDetail(item.goodsId)">
            <div class="good-img"><img :src="imgUrl+item.goodsImg"></div>
            <div class="good-details">
                <p>{{item.goodsName}}</p>
                <p>价格：￥{{item.goodsPrice}}.00</p>
            </div>
        </a>
            <div class="fav-state"><a href="#" @click="delFav(item._id)">删除</a></div>
        </div>
    </div>
    <FooterBar></FooterBar>
  </div>
</template>
<script>
import FooterBar from '../common/footer.vue'
import { Toast } from 'mint-ui'
export default {
  name:"fav",
  components:{
        FooterBar
    },
    data(){
        return{
            favList:[]
        }
    },
    // 身份验证
  beforeRouteEnter:(to, from, next) => {
     if (localStorage.getItem('tokenId')) {
         next()
     } else {
         Toast('请登录')
         next('/login')
     }
  },
  mounted(){
      this.getFavData()
  },
  methods: {
      // 获取收藏夹列表
      getFavData(){
          this.$http.post('/collectionGet',{
              tokenId:localStorage.getItem('tokenId')
          })
          .then(res=>{
              //console.log(res)
              this.favList = res.data.list
          })
          .catch(err=>{
              console.log(err)
          })
      },
      // 删除功能
      delFav(id){
          this.$http.post('/collectionRemove',{
              tokenId:localStorage.getItem('tokenId'),
              collectionId:id
          })
          .then(res=>{
              //console.log(res)
               this.getFavData()
          })
          .catch(err=>{
              console.log(err)
          })
      },
      // 点击跳转详情
      jumpDetail(goodsId){
          this.$router.push({path:'/detail',query:{id:goodsId}})
      }
  }
}
</script>
<style scoped>
.mint-header{background-color: #e12f2f}
.fav-main{padding-top: 40px;background: #f8f8f8;padding-bottom: 150px}
.fav-item{border-top: 1px solid #f1f1f1;padding:10px;overflow:hidden;margin-top: 10px;background: #fff}
.fav-item > a{display: block;height: 100%}
.good-img{width: 100px;height: 80px;margin:0 10px;float: left}
.good-details{width: calc(100% - 120px);float: left;line-height: 2}
.fav-state{clear: both;text-align: right;}
.fav-state a{color: #a2a2a2}
</style>




