<template>
  <div>
     <header-top></header-top>
     <div class="container list-scroll">
     <div class="banner"><img src="static/img/n1s.jpg"></div>
        <mt-loadmore       
        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
        :auto-fill="false" ref="loadmore">
          <ul>
              <li v-for="car in carList"><a href="javascript:void(0)" @click="showDetails(car._id)"><img :src="imgUrl+car.goodsImg"></a></li>
          </ul>
        <div slot="bottom" class="mint-loadmore-bottom">
          <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">上拉加载更多……</span>
        </div>
        </mt-loadmore>
     </div>
    <FooterBar></FooterBar>
  </div>
</template>
<script>
import HeaderTop from '../common/header.vue'
import FooterBar from '../common/footer.vue'
import { Toast } from 'mint-ui';
export default {
  name:"carlist",
  components:{
		HeaderTop,
		FooterBar
    },
    data () {
        return {
            carList:[],
            pageNum:4,      //数量
            pagssize:1,     //页码
            allLoaded: false,//数据是否加载完毕
            bottomStatus: '',//底部上拉加载状态
        }
    },
    mounted () {
        this.getData()
    },
    methods: {
        getData(){
            this.$http.get('/goodslist',{
                params:{
                    num:4,				    // 每个显示个数
					goodsType:1,		    // 商品类别
					page:1	                // 页码
                }
            })
            .then(res=>{
               // console.log(res)
                this.carList = res.data.list
            })
        },
        loadBottom() {
        this.pagssize += 1;
         this.$http.get('/goodslist',{
                params:{
                    num:4,
					goodsType:1,
					page:this.pagssize
                }
            })
            .then(res=>{
                if (res.data.errCode==1) {
                    Toast({
                    message: res.data.errMsg,
                    position: 'bottom',
                    duration: 3000
                    });
                    return false
                }

                let arr = res.data.list
                for (let i = 0; i < arr.length; i++) {
                   this.carList.push(arr[i])
                }
            })
            .catch(error=>{
				Toast("服务器无响应，商品列表获取失败");
            })
      },
        // 点击跳转详情页
      showDetails(thisId){
            this.$router.push({path:'/detail',query:{id:thisId}})
         }   
    }
}
</script>
<style scoped>
.list-scroll{padding-bottom: 80px}
.list-scroll li{border-bottom: 20px solid #fafafa}
</style>



