<template>
  <div>
    <mt-header title="商品详情" fixed>
        <router-link to="/carlist" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <div class="container detail-main">
        <div class="goods-detail">
            <dl>
                <dt><img :src="goodsImg"></dt>
                <dd>名称：<em>{{goodsName}}</em></dd>
                <dd>价格：<em>￥{{goodsPrice}}</em></dd>
                <dd>颜色:<em>{{goodsColor}}</em></dd>
                <dd>数量：<span><a href="#" @click="goodsNum>1?goodsNum--:1">-</a><input type="number" 
                v-model="goodsNum"><a href="#" @click="goodsNum++">+</a></span></dd>
            </dl>
            <div class="goods-info">
                <p>{{goodsInfo}}</p>
            </div>
        </div>
    </div>
    <div class="buy">
        <span>合计：￥{{totalPrice()}}</span><span :class='active?"":"active"' @click="addFav">加入收藏</span><span><a href="javascript:void(0)" @click="postOrderList">立即购买</a></span>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name:'detail',
  data(){
      return{
        active:true,
        goodsId:'',
        favId:'',
        goodsImg:'../../assets/loading.gif',
        goodsName:'',
        goodsPrice:'',
        goodsColor:'',
        goodsNum:1,
        goodsInfo:'',
        orderList:''
      }
  },
  mounted () {
      this. getFavData()
      this.getGoodsDetail()
  },
  methods:{
    // 身份验证
    beforeRouteEnter:(to, from, next) => {
        if (localStorage.getItem('tokenId')) {
            next()
        } else {
            Toast('请登录')
            next('/login')
        }
    },
    // 获取商品详情
    getGoodsDetail(){
          this.$http.get('/goodscon',{
              params:{
                  goodsId:this.$route.query.id
              }
          })
          .then(res=>{
            this.goodsId = res.data.list[0]._id,
            this.goodsImg = this.imgUrl +res.data.list[0].goodsImg,
            this.goodsName = res.data.list[0].goodsName,
            this.goodsPrice = res.data.list[0].goodsPrice,
            this.goodsColor = res.data.list[0].goodsColor,
            this.goodsNum = res.data.list[0].goodsNum,
            this.goodsInfo = res.data.list[0].goodsInfo
          })
          .catch(err=>{
              Toast("服务器无响应，商品详情信息获取失败");
          })
      },
      // 获取收藏列表
        getFavData(){
          this.$http.post('/collectionGet',{
              tokenId:localStorage.getItem('tokenId')
          })
          .then(res=>{
            console.log(res)
            if (res.data.errCode == 0) {
                let arr = res.data.list
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i].goodsId == this.goodsId) {   
                            this.active = false
                            this.favId = arr[i]._id
                    }
                }
            }
          })
      },
      // 合计
       totalPrice(){
          return this.goodsNum * this.goodsPrice
      },
    // 提交订单数据
    postOrderList(){
        //整合订单数据
        this.orderList = {
            goodsImg:this.goodsImg,
            goodsName:this.goodsName,
            goodsPrice:this.goodsPrice,
            goodsColor:this.goodsColor,
            goodsNum:this.goodsNum
        }
        this.$http.post('/orderlistAdd',{
                tokenId:localStorage.getItem('tokenId'),
                orderList:this.orderList,
                orderTotal:this.totalPrice()
        })
        .then(res=>{
            Toast({
            message: '订单成功提交',
            position: 'center',
            duration: 3000
          });
          this.orderList = ''
          this.$router.push('/order')
        })
        .catch(err=>{
            Toast("服务器无响应，订单提交失败");
        })
    },
    addFav(){
        //console.log(this.goodsId)
        if (this.active) {
            this.active = false
            // 执行添加收藏夹
        this.$http.post('/collectionAdd',{
              tokenId:localStorage.getItem('tokenId'),
              goodsId:this.goodsId
          })
          .then(res=>{
              Toast("商品收藏成功");
              console.log(res)
          })
          .catch(err=>{
              console.log(err)
          })
        } else {
            this.active = true
            // 执行移除收藏夹
            
            this.$http.post('/collectionRemove',{
              tokenId:localStorage.getItem('tokenId'),
              collectionId: this.favId
            })
            .then(res=>{
                Toast("商品移除成功");
                //console.log(res)
            })
            .catch(err=>{
                console.log(err)
            })
        }
    }
  }
}
</script>
<style scoped>
.mint-header{background-color: #e12f2f}
.detail-main{padding-top: 40px;padding-bottom: 45px}
.goods-detail dt{border-bottom: 1px solid #efefef}
.goods-detail dd{line-height: 2;padding: 0 10px;font-size: 16px}
.goods-detail dd:nth-child(2){font-weight: bold;font-size: 18px}
.goods-detail dd:nth-child(3){color: #e12f2f}

.goods-detail span:last-child{border: 1px solid #ccc;border-radius: 5px;display: inline-block;height: 24px;margin-right: 10px;vertical-align: bottom;vertical-align: middle;text-align: center;line-height: 24px}
.goods-detail span:last-child a{display: inline-block;width: 30px;}
.goods-detail span:last-child input{width: 30px;height: 22px;vertical-align: top;text-align: center;border-right:1px solid #ccc;border-left: 1px solid #ccc;}
.goods-info{border-top: 1px solid #efefef;margin-top: 10px;overflow: hidden;padding: 10px;text-indent: 2em;line-height: 2}

.buy{width: 100%;position: fixed;bottom: 0;height: 45px;z-index: 1000;line-height: 45px;border-top: 1px solid #efefef;background-color: #fafafa;text-align: right;font-size: 12px}
.buy span:nth-child(1){float: left;padding-left: 10px;color:#e12f2f}
.buy span:nth-child(2){display: inline-block;text-align: center;padding: 0 15px;background-color: #ffc507;border-left: 1px solid #efefef;color: #fff;vertical-align: top;}
.buy span:last-child{display: inline-block;width: 30%;background-color: #e12f2f;text-align: center;color: #fff;font-size: 16px}
.buy span a{color:#fff}
.buy .active{background:#ccc !important;color: #7b7b7b !important}
</style>



