<template>
  <div>
    <mt-header title="用户中心" fixed>
        <router-link to="/" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <div class="user-info">
        <div class="user-name">
            <i class="icon iconfont icon-diandongche"></i><em>{{userCenter.userName}}</em>
            <span @click="loginOut">退出</span>
        </div>
        <mt-cell title="电话号码" ><span style="color: green">{{userCenter.usertel}}</span></mt-cell>
        <mt-cell title="收藏夹" is-link to="/fav">{{userCenter.collection}}</mt-cell>
        <mt-cell title="订单列表" is-link to="/order"><span style="color: green"></span>{{userCenter.order}}</mt-cell>
    </div>
    <FooterBar></FooterBar>
  </div>
</template>
<script>
import FooterBar from "../common/footer.vue"
import { Toast } from 'mint-ui'
import { MessageBox } from 'mint-ui'
export default {
  name: "user",
  components: {
    FooterBar
  },
  data(){
      return{
          userCenter:[]
      }
  },
  methods: {
    loginOut() {
      MessageBox.confirm("确定执行此操作?").then(action => {
        if (action) {
          localStorage.removeItem("tokenId");
          this.$router.push("/");
        }
      });
    },
    // 获取个人中心数据
    getUserCenter(){
          this.$http.post('/userCenter',{
              tokenId:localStorage.getItem('tokenId')
          })
          .then(res=>{
              //console.log(res.data.list)
              this.userCenter = res.data.list
          })
      }
  },
  // 身份验证
  beforeRouteEnter:(to, from, next) => {
     if (localStorage.getItem('tokenId')) {
         next()
     } else {
         Toast('请登录')
         next('/login')
     }
  },
  mounted(){
      this.getUserCenter()
  }
};
</script>
<style scoped>
.mint-header {
  background-color: #e12f2f;
}
.user-info {
  margin-top: 40px;
}
.user-name {
  padding: 20px 10px;
  border-bottom: 1px solid #fafafa;
}
.user-name i {
  font-size: 36px;
  background: #f3f3f3;
  border-radius: 50%;
  vertical-align: middle;
  color: #e12f2f;
}
.user-name em {
  margin-left: 10px;
}
.user-name span {
  float: right;
  padding: 5px 10px;
  background: #efefef;
  border-radius: 3px;
  margin-top: 5px;
}
</style>





