<template>
  <div id="app">
      <router-view/>
  </div>
</template>

<script>
import HeaderTop from './components/common/header.vue'
import FooterBar from './components/common/footer.vue'
export default {
  name: 'App'
}
</script>
<style>
/* 公共 */
html,body{
    max-width: 750px;
   	min-width: 320px;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
}
/* 通用滑屏容器 */
.container{
   width: 100%;
   height: 100%;
   -webkit-overflow-scrolling: touch;
   overflow-x: hidden;
   overflow-y: scroll;
   position: absolute;
   z-index: 0;
}
.container::-webkit-scrollbar{
	display: none;
}
#app{height: 100%}
</style>



