import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/pages/main.vue'
import Order from '@/components/pages/order.vue'
import Login from '@/components/pages/login.vue'
import Reg from '@/components/pages/reg.vue'
import Fav from '@/components/pages/fav.vue'
import CarList from '@/components/pages/carlist.vue'
import Detail from '@/components/pages/detail.vue'
import User from '@/components/pages/user.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/main'
    },
    {
      path: '/main',
      name: 'mainArea',
      component: Main
    },
	  {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/reg',
      name: 'reg',
      component: Reg
    },
    {
      path: '/fav',
      name: 'fav',
      component: Fav
    },
    {
      path: '/carlist',
      name: 'carlist',
      component: CarList
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail
    },
    {
      path: '/user',
      name: 'user',
      component: User
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



