<template>
  <mt-header fixed>
    <span slot="left"><a href="/main"><img src="../../assets/logo.png"></a></span>
    <span slot="right">
      <router-link to="/fav" class="cart"><i class="icon iconfont icon-favorite"></i></router-link>
      <router-link to="/login" class="user-icon"><i class="icon iconfont icon-yonghu"></i></router-link>
      </span>
  </mt-header>
</template>
<script>
    export default{
      name:'headerBar'
    }
</script>
<style scoped>
.mint-header{background: #f7f7f7;position: relative}
.is-left span{display: inline-block;}
.is-left a{display: inline-block;width: 108px;height: 23px}
.is-right a{display:inline-block}
.is-right i{color: #999;font-size:24px;}
.cart i{font-size: 26px}
.is-right a:first-child{margin-right: 15px}
.arrow{border-top: 1px solid transparent;border-right: 4px solid transparent;border-bottom: 4px solid rgba(0, 0, 0, 0.7);border-left: 4px solid transparent;width: 0;position:absolute;top: -5px;right: 20%}
.user-login{background:#000;position: absolute;right:0;top: 40px;border-radius: 5px;width: 30%;text-align: center;text-indent: 20px}
.user-login li a{color: #eee;display: block;padding:10px 0;}
</style>



