<template>
	<div>
		<header-top></header-top>
		<div class="main">
				<div class="container main-scroll">
				<carousel></carousel>
				<nav>
					<ul>
						<li><router-link to="/carlist"><i class="icon iconfont icon-diandongche01"></i><span>整车热卖</span></router-link></li>
						<li><a href="#"><i class="icon iconfont icon-remai"></i><span>热卖配件</span></a></li>
						<li><a href="#"><i class="icon iconfont icon-mendian"></i><span>线下门店</span></a></li>
						<li><a href="#"><i class="icon iconfont icon-zhoubianyou"></i><span>文化周边</span></a></li>
					</ul>
				</nav>
				<div class="hot-car">
					<h2>整车热卖</h2>
					<div class="car-list">
						<div class="recommend"><img src="static/img/n1s.jpg"></div>
						<div class="rec-list">
							<ul>
								<li v-for="(car,index) in carList">
									<a href="javascript:void(0)" @click="showDetails(car._id)"><img :src="imgUrl+car.goodsImg"></a>
								</li>
							</ul>
						</div>
					</div>
				</div>
				<div class="hot-parts">
					<h2>热卖配件</h2>
					<div class="parts-list">
						<div class="rec-list">
							<ul>
								<li
								v-for="part in partList"
								><a href="#"><img :src="imgUrl+part.goodsImg"></a></li>
							</ul>
						</div>
					</div>
				</div>			
			</div>		
		</div>
		<FooterBar></FooterBar>
	</div>
</template>
<script>
import HeaderTop from '../common/header.vue'
import FooterBar from '../common/footer.vue'
import Carousel from '../common/carousel.vue'
import { Toast } from 'mint-ui';
export default{
	name:'mainArea',
	components:{
		HeaderTop,
		FooterBar,
		Carousel
	},
	data () {
		return {
			carList:[],
			partList:[]
		}
	},
	mounted () {
		// 获取商品数据
		this.getData()
	},
	methods: {
		// 获取整车、配件数据
		getData(){
			this.$http.all([
				this.$http.get('/goodslist',{
					params:{
					num:4,				// 每个显示个数
					goodsType:1,		// 商品类别
					page:1				// 页码
					}
				}),
				this.$http.get('/goodslist',{
					params:{
					num:4,
					goodsType:2,
					page:1
					}
				})
			])
			.then(this.$http.spread((carRes,partRes)=>{
				//console.log(carRes.data)
				//console.log(partRes.data)
				if (carRes.data.errCode !== 0) {
					Toast(carRes.data.errMsg);
					return false
				}
				if (partRes.data.errCode !== 0) {
					Toast(partRes.data.errMsg);
					return false
				}
				this.carList = carRes.data.list
				this.partList = partRes.data.list
			}))
			.catch(error=>{
				Toast("服务器无响应，商品列表获取失败");
			})
		},
		  // 点击跳转详情页
      showDetails(thisId){
            this.$router.push({path:'/detail',query:{id:thisId}})
         }   
	}
}
</script>
<style scoped>
.main-scroll{padding-bottom: 50px}
nav{width: 100%;overflow: hidden;padding:10px 5px;background-color: #f3f3f3;}
nav li{float: left;width: 25%;text-align: center}
nav li a{display: inline-block}
nav li i{color: #e12f2f;font-size: 32px}
nav li span{display: block;margin-top: 5px}

.hot-car{margin: 10px 0}
.main-scroll h2{font-size: 18px;margin: 5px;font-weight: bold;color: #e12f2f;padding-bottom: 10px;border-bottom: 1px solid #efefef}
.rec-list li{float: left;width:100%;}
</style>


