<template>
  <div class="user-info">
    <div class="logo"><a href="/main"><img src="../../assets/logo.png"></a></div>
    <h2>用户注册</h2>
    <div class="user-reg">
      <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password" ></mt-field>
      <mt-field label="确认密码" placeholder="请确认密码" type="password" v-model="password2"></mt-field>
      <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
      <span class="btn">
         <mt-button type="primary" size="small" @click.native="subReg">注  册</mt-button>
         <mt-button type="danger" size="small" @click.native="resetFrom">重  置</mt-button>
      </span>
       <p><router-link to="/login">我有账号</router-link></p>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name:'reg',
  data () {
    return {
      username:'',
      password:'',
      password2:'',
      phone:''
    }
  },
  methods: {
    subReg(){
      // 表单验证
      if (this.username=='') {
        Toast({
            message: '用户名不能为空',
            position: 'top',
            duration: 2000
          });
          return false
      }
      if (this.password=='') {
        Toast({
            message: '密码不能为空',
            position: 'top',
            duration: 2000
          });
          return false
      }else{
        if (this.password!=this.password2) {
          Toast({
            message: '两次密码不一致',
            position: 'top',
            duration: 2000
          });
          return false
        }
      }
      if (this.phone=='') {
        Toast({
            message: '手机号不能为空',
            position: 'top',
            duration: 2000
          });
          return false
      }else{
         let pattern = /^1[34578]\d{9}$/;
         if (!pattern.test(this.phone)) {
          Toast({
            message: '手机号格式不正确',
            position: 'top',
            duration: 2000
          });
          return false
        }
      }
      // 提交注册
      this.$http.post('/reg',{
        userName:this.username,
        passWord:this.password,
        userTel:this.phone
      })
      .then(response=>{
          console.log(response)
          if (response.data.errCode == 1) {
              Toast({
              message: response.data.errMsg,
              position: 'top',
              duration: 2000
              });
              return false
          }
          if (response.data.errCode == 3) {
              Toast({
              message: response.data.errMsg,
              position: 'top',
              duration: 2000
              });
              return false
          }
          Toast({
            message: response.data.errMsg,
            position: 'top',
            duration: 2000
          });
          this.$router.push('/login')
      }).catch(error=>{
           Toast({
            message:'服务器无响应',
            position: 'center',
            duration: 2000
          });
      })
    },
    // 重置表单
    resetFrom(){
      this.username = this.password = this.password2 = this.phone = ''
    }
  }
}
</script>
<style scoped>
.user-info{width: 80%;margin: 0 auto;text-align: center}
.logo{width: 60%;margin: 0 auto;margin-top: 20%}
.user-info h2{font-size: 16px;margin: 50px 0 20px 0;color: #666}
.btn{display: inline-block;margin-top: 20px}
.btn button{margin: 0 10px;margin-bottom: 10%}
</style>



