<template>
  <div>
    <mt-header title="我的订单" fixed>
        <router-link to="/" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <div class="container order-main">
        <div class="order-list" v-for="item in orderList">
          <div class="order-id">订单编号：{{item._id}} <span>已支付</span></div>
          <div class="goods-info">
              <div class="goods-show">
                  <a href="#">
                     <dl>
                      <dt><img :src="item.goodslist.goodsImg"></dt>
                      <dd>{{item.goodslist.goodsName}}</dd>
                      <dd>颜色：{{item.goodslist.goodsColor}}</dd>
                      <dd>数量：{{item.goodslist.goodsNum}}</dd>
                      <dd>￥{{item.goodslist.goodsPrice}}.00</dd>
                    </dl>
                  </a>
              </div>
          </div>
          <div class="deal"><span>合计：<em>￥{{item.ordertotal}}.00</em> (含运费￥0.00)</span></div>
        </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
  export default {
    name:'order',
    data(){
      return{
        orderList:[]
      }
    },
    mounted () {
      this.getOrderList()
    },
    methods:{
      // 获取订单列表数据
      getOrderList(){
        this.$http.post('/orderlistGet',{
          tokenId:localStorage.getItem('tokenId')
        })
        .then(res=>{
          this.orderList = res.data.list
        })
        .catch(err=>{
          Toast("服务器无响应，订单列表获取失败");
        })
      }
    },
    // 身份验证
  beforeRouteEnter:(to, from, next) => {
     if (localStorage.getItem('tokenId')) {
         next()
     } else {
        Toast('请登录')
         next('/login')
     }
  }
  }
</script>
<style scoped>
.mint-header{background-color: #e12f2f}
.order-main{padding-top: 40px;}
.order-list{margin-bottom:20px;background-color: #f8f8f8}
.order-id{height: 35px;line-height: 35px;background: #fff;padding: 0 10px}
.order-id span{float: right;color: #e12f2f}
.goods-show{padding: 5px 10px;overflow: hidden;border-bottom: 2px solid #fff;}
.goods-show > a{display: inline-block}
.goods-show dt{float: left;margin-right: 5px;width: 100px;height:80px;background: #fff}
.goods-show dd{float:right;width: calc(100% - 110px);line-height:1.5}
.goods-show dd:last-child{color: #e12f2f;text-align: right;padding-left: 10%}
.deal{line-height: 35px;padding: 0 10px;background-color: #fafafa;overflow: hidden}
.deal span{float: right}
.deal span em{color: #e12f2f}
</style>




