<template>
  <div class="user-info">
    <div class="logo"><a href="/main"><img src="../../assets/logo.png"></a></div>
    <h2>用户登录</h2>
    <div class="user-reg">
      <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password" ></mt-field>
      <span class="btn">
         <mt-button type="primary" size="small" @click.native="subLogin">登   录</mt-button>
         <mt-button type="danger" size="small" @click.native="resetFrom">重  置</mt-button>
      </span>
    </div>
    <p><router-link to="/reg">还没账号？</router-link></p>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name:'login',
  data () {
    return {
      username:'',
      password:''
    }
  },
  methods: {
    subLogin(){
      // 表单验证
      if (this.username=='') {
        Toast({
            message: '用户名不能为空',
            position: 'top',
            duration: 2000
          });
          return false
      }
      if (this.password=='') {
        Toast({
            message: '密码不能为空',
            position: 'top',
            duration: 2000
          });
          return false
        }
      // 提交登录信息
      this.$http.post('/login',{
        userName:this.username,
        passWord:this.password
      })
      .then(response=>{
          //console.log(response)
          if (response.data.errCode != 0) {
             Toast({
              message: response.data.errMsg,
              position: 'top',
              duration: 2000
            });
            return false
          }
          localStorage.setItem('tokenId',response.data.tokenId)
          this.$router.push('/main')
      }).catch(error=>{
           Toast({
            message:'服务器无响应',
            position: 'center',
            duration: 2000
          });
      })
    },
    // 重置表单
    resetFrom(){
      this.username = this.password = ''
    }
  }
}
</script>
<style scoped>
.user-info{width: 80%;margin: 0 auto;text-align: center;}
.logo{width: 60%;margin: 0 auto;margin-top: 20%}
.user-info h2{font-size: 16px;margin: 50px 0 20px 0;color: #666}
.btn{display: inline-block;margin-top:20px}
.btn button{margin: 0 10px;margin-bottom: 10%}
</style>



